      </div>

    </custom-collapse>
    <div class="loading" v-if="sessions === undefined" v-loading="true"></div>
    <template v-else>
      <custom-collapse v-for="(session, i1) in sessions" :key="i1" :title="session.name">
        <ul class="articleList">
          <li v-for="(article, i2) in session.children" :key="i2">
            <router-link
              :to="`${articlePathBase}${article.url}`" v-html="article.name">
            </router-link>
          </li>
