<script>

import CustomCollapse from '@/components/collapse/Collapse';
import loadMarkdown from '@/utils/load-markdown';
import envs from '@/utils/envs';
import { i18n } from '@/i18n';
import { getContextUser } from '@/loaders/context';


function getFirstText(el) {
  for (let i = 0; i < el.childNodes.length; i++) {
    const node = el.childNodes[i];
    if (node.nodeName === '#text') {
      return node.nodeValue;
    }
  }
  return undefined;
}

function scopeQuerySelector(el, selector) {
  const elems = el.querySelectorAll(selector);
  for (let i = 0; i < elems.length; i++) {
    const elem = elems[i];
    if (elem.parentNode === el) {
      return elem;
    }
  }
  return undefined;
}

function parseUlToList(ul) {
  const result = [];
  for (let i = 0; i < ul.children.length; i++) {
    const child = ul.children[i];
    const a = scopeQuerySelector(child, 'a');
    const node = a
      ? {
        name: a.innerHTML,
        url: a.getAttribute('href').replace(/\.md$/, ''),
      } : {
        name: getFirstText(child),
        url: undefined,
      };
    const childUl = child.querySelector('ul');
    if (childUl) {
      node.children = parseUlToList(childUl);
    }
    result.push(node);
  }
  return result;
}


export default {
  components: { CustomCollapse },
  data() {
    return {
      sessions: undefined,
    };
  },
  computed: {
    /** @return { string } */
    articlePathBase() {
      const endsWithSlash = this.$route.path.endsWith('/');
      return `${this.$route.path}${endsWithSlash ? '' : '/'}`;
    },
    isCloth() {
      const user = getContextUser();
      return user && user.businessType === 2;
    },
  },

  async created() {
    let md;
    if (this.isCloth) {
      md = await loadMarkdown(`${envs.VUE_APP_HELP_CENTER_URL}/cloth/cn/README.md`, '?v=20200914');
    } else {
      md = await loadMarkdown(`${envs.VUE_APP_HELP_CENTER_URL}/${i18n.language}/README.md`, '?v=20200731');
    }
    const { data: dom } = md;
    this.sessions = parseUlToList(dom.querySelector('ul'));
  },
};
</script>
